@media only screen and (max-width: 600px) {

  .pv1--bg-hero{
    min-height: 500px;
  }

  #alunos{
    .flex-title{
      padding-top: 20px!important;
    }
  }

  .pv1--for-peoples{
    margin-bottom: 20px!important;
    .pv1--card{
      margin-bottom: 15px!important;
      width: 100%;
      flex: inherit;
    }
  }
  //.menu {
  //  display: none;
  //}
  .pv1--bg-hero {
    .headline {
      margin: 15px auto;

      h1 {
        font-size: 23px;
        line-height: 1.2;
      }

      p {
        font-size: 17px;
      }
    }
  }




  .pv1--flex-items-list-course {
    padding: 8px;

    div {
      width: calc(50% - 10px) !important;
      margin: 0 5px;
    }
  }

  .custom-size {
    height: auto !important;
  }

  .pv1--ct-shape .shape-bottom svg {
    width: 250vw;
    height: 40px;
  }

  .pv1--section {
    flex-wrap: wrap;
    padding: 16px;
    margin: 0 auto;
  }

  .pv1--oferta{
    padding: 10px;
    .price p{
      padding: 5px 15px;
      font-size: 1.2em!important;
    }
  }

  .pv1--section#oferta h1{
    font-size: 26px;
    line-height: 33px;
  }

  .pv1--head-title{
    font-size: 1.6em!important;
  }

  .pv1--sub-title{
    font-size: 1.2em;
    margin-bottom: 8px;
  }

  .pv1--bonus{
    h1.head-title{
      font-size: 24px!important;
      line-height: 33px;
    }
    p{
      font-size: 1em!important;
    }
  }

  .pv1--section#cert{
    h1{
      font-size: 26px;
      line-height: 33px;
    }
    h2{
      font-size: 15px;
      color: #4f4e4e;
    }
  }

  .pv1--#garantia{
    font-size: 0.85em;
  }


  .section .col{
    flex: 100%;
  }

  .btn-conversion {
    min-width: 80%;
    max-width: 90%!important;
    padding: 12px;

  }
}
