

$gradientpv2: linear-gradient(to right, #3cc871, #11998e);

@mixin btn-conversion-pv2 {
  font-size: 1.5em;
  font-weight: 600;
  text-shadow: 0px 0px 10px rgba(23, 77, 0, 0.91);
  fill: #fff;
  color: #fff;
  background-color: transparent;
  background-image: linear-gradient(30deg, #ff6641 56%, #ff7925 100%);
  border-style: solid;
  border-width: 0 0 4px;
  border-color: #bd391f;
  border-radius: 15px;
  padding: 16px 20px;
  width: 100%;
  margin: 12px auto;
  text-decoration: none;
  display: block;
  max-width: 770px;
  transition: ease-in 0.3s;

  &.v2 {
    background: linear-gradient(to right, #3cc871, #11998e);
    border-color: #11998e;
  }

  &:hover {
    border-color: transparent;
    box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.5);
    transform: scale(1.1);
    color: #fff;
  }
}

.pv2 {
  &--hero {
    text-align: center;
    color: #fff;
    padding: 20px;

    &.gradient {
      background: $gradientpv2;
      background-attachment: fixed;
      background-position: center;
      background-size: cover;
      width: 100%;
      height: auto;
    }
  }

  &__sub-headline {
    font-size: 1.5em;
    color: #fff;
    max-width: 500px;
    margin: 0 auto;
  }

  &__headline {
    max-width: 1000px;
    margin: 0 auto;

    .title {
      font-weight: 800;
      font-size: 2.3em
    }


    .text {
      font-size: 1.2em;
      margin: 20px 0 0 0;
    }
  }

  &__vsl{
    @include vls;
  }

  &__btn {
    &--conversion {
     @include btn-conversion-pv2;
    }
  }
}

.pv2--oquee{
  background: #ffffff;
  position: relative;
  padding: 25px;

  &__colum {
    width: 100%;
    margin: 16px;
  }

  &__container {
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
    justify-content: space-between;
    align-content: center;
    padding: 60px 0;
    align-items: center;
    p {
      font-size: 1.2em;
      color: #54595f;
      text-align: left;
      line-height: 1.7;
    }
  }

  &__headline{
    font-weight: 800;
    color: #54595f;

    span {
      color: #139c8c;
      text-transform: capitalize;
    }
  }
  &__btn-conversion{
    @include btn-conversion-pv2
  }

}

