    border-radius: 20px;
    position: relative;
    box-shadow: 0px 2px 8px #dfdfdf;
    background: #fff;

    .header {
      position: absolute;
      top: -20px;
      border-radius: 50px;
      padding: 4px 8px;
      text-align: center;
      width: 50%;
      margin: 0 auto;
      background: #727272;
      font-weight: 700;
      color: #fff;
      right: 50%;
      left: 50%;
      transform: translate(-50%, 0px);
    }

    del {
      color: #F44336;
    }

    p {
      color: #4f4e4e;
      font-size: 1.2em;

      &.first {
        text-decoration: underline;
        line-height: 1.5;
      }

      &.end {

      }
    }

    h1 {
      color: #0cac8a;
      font-weight: 700;
      font-size: 5em;
    }
  }

}

