@media only screen and (max-width: 990px) {
  .pv2--#oquee .container{
    flex-wrap: wrap;
    padding: 16px;
  }
  .pv2--#conteudo .cards .card{
    width: calc(50% - 40px);
  }
  .pv2--#cardsBg .cards {
    margin: 0;
    .card{
      width: calc(50% - 30px);
    }
  }
  .pv2--#paraquem .cards .simple-card{
    width: calc(50% - 30px);
  }
}

@media only screen and (max-width: 650px) {



    .pv2__sub-headline {
      line-height: 1.2;
      font-size: 1.3em;
    }

  .pv2__headline {
      h1.title {
        font-size: 1.7em;
      }
    }


  .pv2--oquee#oquee {
    padding: 16px;

    .pv2--oquee__container {
      flex-wrap: wrap;
      padding: 0;
    }
    .custom-shape-divider-bottom-1683417921 {
      display: none;
    }
  }

  #pv2--conteudo .pv2--cards .pv2--card{
    width: 100%;
  }
  .pv2--professor#professor .container{
    flex-wrap: wrap;
    .colum{
      width: 100%;
      &.image{
        text-align: center;
      }
      img{
        margin: 0 auto;
        max-width: 90%;
      }
    }
  }

  .pv2--cardsBg#cardsBg{
    .cards{
      margin: 0px;
      .card{
        width: 100%;
      }
    }
  }

  .pv2--duvida#duvida{
    padding: 16px;
  }

  .pv2--#paraquem{
    padding: 16px;
    .cards{
      .simple-card{
        width: 100%;
      }
    }
  }
  .pv2--#deps{
    margin: 0;
    padding: 16px;
  }

  .pv2--#bonus{
    padding: 16px;
    .bonus-item{
      flex-wrap: wrap;
      padding: 8px!important;
      .image, .desc{
        width: 100%;
      }
    }
  }

  .pv2--#price{
    .price-box {
      h1{
        font-size: 3em;
      }

      p{
        margin: 0;
      }
    }
  }
  .pv2--#garantia{
    .item{
      flex-wrap: wrap;
    }
  }
  .pv2--#faq{
    padding: 16px;
    .container{
      flex-wrap: wrap;
      .desc{
        max-width: 100%;
        padding: 0;
        h1{
          max-width: 100%;
          font-size: 1.7em;
        }
      }
    }
  }
}



